<script>
  import { createEventDispatcher } from "svelte";

  export let date;
  export let time;
  export let title;
  export let check = true;
  export let minDate;

  let isFocused = false;

  const dispatch = createEventDispatcher();

  function dispatchValues() {
    dispatch(title.replace(/\s+/g, "").toLowerCase(), {
      date: date,
      time: time
    });
  }
</script>

<style type="text/postcss">.input{font-size:1.5rem;text-transform:lowercase;border-bottom-width:4px;color:#edf2f4;background-color:transparent;margin-bottom:1rem;min-width:35%}.input-ok{border-color:#2ec4b6}.input-error{border-color:#ef233c}.background{background-color:#2b2d42;color:#8d99ae}.background-selected{color:#ff9f1c}label{font-size:.875rem;font-weight:500}</style>

<div class="background p-4 {isFocused ? 'background-selected' : ''}">
  <h1>
    {title}
    <body>
      <div class="flex-wrap">
        <label>DATE</label>
        <input
          class="input {check ? 'input-ok' : 'input-error'}"
          type="date"
          bind:value={date}
          on:focus={() => (isFocused = true)}
          on:blur={() => (isFocused = false)}
          on:change={dispatchValues}
          min={minDate} />
      </div>
      <div class="flex-wrap">
        <label>TIME</label>
        <input
          class="input {check ? 'input-ok' : 'input-error'}"
          type="time"
          bind:value={time}
          on:focus={() => (isFocused = true)}
          on:blur={() => (isFocused = false)}
          on:change={dispatchValues} />
      </div>
    </body>
  </h1>
  <slot />
</div>
